body{
  padding-top: 0;
}

@font-face {
  font-family: "acalog-icons";
  font-style: normal;
  font-weight: normal;
  src: url("https://acalog-clients.s3.amazonaws.com/global/fonts/acalog-icons.ttf") format("truetype");
}

$acalog-font-family: Arial,Verdana,Helvetica,sans-serif;
.acalog-content{
  .acalog-catalog-name{
    font-family: $acalog-font-family;
    font-size: 16px;
    color: $black;
  }

  .acalog-catalog-archived-alert{
    font-family: $acalog-font-family;
    font-size: 18px;
    color: $red;
  }

  .highlight_1 {
    font-family: $acalog-font-family;
    font-size: 16px;
    color: red;
  }

  h1, h2, h3, h4, h5, h6 {
    font-family: $acalog-font-family;
    color: $black;
    margin: 20px 0px 10px 0px;
    text-transform: none;
  }

  h1.acalog-page-title {
    margin: 10px 0;
  }

  h1 {
    font-size: 34px;
  }

  h2 {
    font-size: 28px;
  }

  h3 {
    font-size: 24px;
  }

  h4 {
    font-size: 20px;
  }

  h5 {
    font-size: 18px;
  }

  h6 {
    font-size: 16px;
  }



  p, th, td {
    font-family: $acalog-font-family;
    font-size: 16px;
    color: $black;
  }

  a {
    font-size: 16px;
    color: $red;
    text-decoration: underline;
  }
  th {
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: .05rem;
    text-transform: uppercase;
    line-height: 1.58;
    padding: 1.4rem 1.4rem 1.2rem;
    text-align: left;
    &.th_lt, &.th_rt {
      background-color: $red;
      color: $white;

      a {
        color: $white;
      }
    }
  }
  td {
    &.td_lb, &.td_rb {
      background-color: $red;
      padding: 0;
    }
    &.td_Dark {
      background-color: $gray;
    }
  }
  #global-search-fieldset {
    border-bottom: .1rem solid #f2f2f2;
    margin: 0 0 1.5rem 0;
    legend, .no_display {
      display: none;
    }
    select, input {
      height: 4rem;
      margin: 0 0 1.5rem 0;
      font-family: $acalog-font-family;
      font-size: 16px;
    }

    .n2_search_header {
      display: block;
      border-top: .3rem solid #000;
      padding: 1.4rem 2rem 1.3rem 0;
      margin: 1.5rem 0 1.3rem 0;
      width: 100%;
      font-weight: 600;
      padding: 0.6rem 2rem 0.6rem 0;
      line-height: 1.4;
      font-size: 18px;

      @include break($sm-break) {
        border-top: 0.3rem solid $red;
        padding: 1.4rem 2rem 1.3rem 0;
        margin-bottom: 1.3rem;
      }
    }

    #keyword-submit-icon {
      margin: -47px 12px 0 0;
      float: right;
      width: 18px;
      height: 18px;
      padding: 1px;
      border-width: 1px;
      border-style: solid;
      border-color: transparent;
      display: inline-block;
      position: relative;
      color: #000000;
      font-family: 'acalog-icons';
      font-size: 20px;
      font-weight: normal;
      text-decoration: none;
      line-height: 1;
      .acalog-icons-hidden {
        @extend .sr-only;
      }
    }

    #exact_match_n2{
      display: inline-block;
      margin: 0 3px 0 0;
      width: auto;
      vertical-align: middle;
    }

    label.n2_search {
      display: inline;
      margin: 0;
      font-family: $acalog-font-family;
      font-size: 14px;
    }

    .n2_search {
      display: block;
      margin: 0 0 1.5rem 0;
    }
  }
  .n2_links {
    padding: .6rem 0;
    a {
      color: #000;
      display: block;
      line-height: 1.3;
      text-decoration: none;
      font-size: 18px;
      -webkit-transition: all .4s ease;
      transition: all .4s ease;
      &:hover {
        -webkit-transform: translate3d(5px,0,0);
        transform: translate3d(5px,0,0);
        -webkit-transition-duration: .5s;
        transition-duration: .5s;
        color: $red;
      }
    }
    &#gateway-nav-current{
      a {
        font-weight: 600;
        color: $red;
      }
    }
    &:last-child {
      margin-bottom: 3rem;
    }
  }
}
